import React, { Component } from 'react';

class AnimateMouse extends Component {
    state = {
        posX: 0,
        pozY: 0,
    }

    setPosition(e) {
        const { clientX, clientY } = e;
        const { ratioX, ratioY } = this.props;
        const centerX = window.innerWidth / 2;
        const centerY = window.innerHeight / 2;

        const posX = clientX - centerX;
        const posY = clientY - centerY;

        this.setState({
            posX: posX * ratioX,
            posY: posY * ratioY
        })
    }

    componentDidMount() {
        document.addEventListener('mousemove', (e) => this.setPosition(e));
    }
    componentWillUnmount() {
        document.removeEventListener('mousemove', (e) => this.setPosition(e));
    }

    render() {
        const { name, src } = this.props;
        const { posX, posY } = this.state;
        return (
            <>
                <img src={src} className={name} alt={name}
                    style={{
                        transform: `translate(${posX}px, ${posY}px)`
                    }}
                />
            </>
        );
    }
}

export default AnimateMouse;